import React from 'react'
import smallLogo from "../Small Logo.png"
import Contact from './contact';

export default class Home extends React.Component{

    styles = {
        back:{
            width: "100%",
            height: "100%",
            backgroundImage: "",
        },
        header:{
            display:"flex",
            width: "100%",
            height: "10%",
            backgroundColor:"orange",
        },
        title: {
            position: "relative",
            top: "25%",
            height: "75%",
            fontSize: "50px",
            font:"copperplate",
            fontWeight:"bold",
            color: "white",
        },
        container:{
            display: "flex",
            justifyContent: "center",
        },
        main:{
            position: "relative",
            top: "2.5%",
            width: "30%",
            height: "95%",
            color: "white",
        },
        options:{

        },
        
        lower : {

        }
    }

    constructor(props){
        super(props);

        this.state = {

        }
    }

    render(){

        document.title = "Ignited Box Studios";

        return (<> 
            <div style={this.styles.back}>
                <div style={this.styles.header}>
                    <img src={smallLogo} alt="Logo" ></img>
                    <div style={this.styles.title}>Ignited Box Studio</div>
                </div>

                <div style={this.styles.container}>
                    <div style={this.styles.main}>
                        Website is currently under construction. Please visit our contact options.
                    </div>
                    <Contact/>
                </div>
            </div>
        </>)
    }
}