import React from 'react'
import Experimental from '../Portfolios/MaximeGautier/Experimental';
import Home from '../IgnitedBox/Home';
import "./style.css"

export default class SimplePath extends React.Component{

    subPaths = {
        "?ignitedbox": <> <Home/> </>,
        "?portfolio/mg/ep" : <><Experimental/></>,
        "?portfolio/mg/pro" : <><Experimental/></>,
    }

    constructor(props){
        super(props);

        this.state = {

        }
    }

    render(){

        let path = window.location.search.toLowerCase();

        if(this.subPaths[path]) return this.subPaths[path];

        return <> <Experimental/> </>
    }
}