import React from 'react'
import icon from "../Small Logo Profile.png"

export default class Contact extends React.Component{

    styles = {
        back:{
            width: "70%",
            height: "50%",
            display:"flex",
            padding: "0px 20px 20px 0px",
        },
        container:{
            position:"relative",
            padding: "0px 20px 20px 0px",
            display: "flex",
            flexDirection: "column",
        },
        title: {
            color: "white",
            fontSize: 20
        },
        main:{
            position: "relative",
            top: "2.5%",
            width: "100%",
            height: "95%",
            color: "white",
        },
        button:{
            borderRadius: 100,
        }
    }

    constructor(props){
        super(props);

        this.state = {

        }
    }

    discordClick(){
        console.log("click");
        window.open("https://discord.gg/BPDZybgFkg");
    }

    render(){

        return (<> 
            <div style={this.styles.back}>
                <div style={this.styles.container}>
                    <div style={this.styles.title}>Discord</div>
                    <button style={this.styles.button} onClick={this.discordClick}>
                        <img style={this.styles.button} src={icon} height={50} ></img>
                    </button>
                </div>
                <div style={this.styles.container}>
                    <div style={this.styles.title}>Temporary Email</div>
                    <div style={this.styles.title}>IgnitedBoxs@gmail.com</div>
                </div>
            </div>
        </>)
    }
}